/** @jsx jsx */
import { jsx, Container } from "theme-ui";
import { useMenus, useDocs } from "docz";
// import { BrowserRouter, Route } from "react-router-dom";
import * as styles from "./styles";

export const MainContainer = ({ children, ...rest }) => {
  const menus = useMenus();
  const components = useDocs();
  const newMenus = [];
  const formatMenus = (menus, newMenus) => {
    menus = menus.map((item) => {
      const { menu } = item;
      if (Array.isArray(menu)) {
        formatMenus(menu, newMenus);
      } else {
        newMenus.push(item);
      }
    });
  };
  formatMenus(menus, newMenus);

  return (
    <Container sx={styles.container} {...rest}>
      {children}
    </Container>
  );
};
export default MainContainer;
