import React from "react";
import { Layout, ConfigProvider } from "antd";
import "antd/dist/antd.css";
import styles from "./index.module.scss";
import { Header as Dheader } from "../Header";
import { MainContainer } from "../MainContainer";
import { SiderBar } from "../Sidebar";

const { Header, Sider, Content } = Layout;

export const Layouts = (props) => {
  return (
    <ConfigProvider dropdownMatchSelectWidth>
      <Layout className={styles.layout}>
        <Header className={styles.header}>
          <Dheader />
        </Header>
        <Layout style={{ background: "#fff", paddingTop: 66 }}>
          <Sider className={styles.sider}>
            <SiderBar />
          </Sider>
          <Content className={styles.content}>
            <MainContainer data-testid="main-container">{props.children}</MainContainer>
          </Content>
        </Layout>
      </Layout>
    </ConfigProvider>
  );
};

export default Layouts;
