import React, { useState, useRef } from "react";
import { Menu } from "antd";
import { useMenus, Link, useCurrentDoc } from "docz";

const { SubMenu } = Menu;

export const SiderBar = (props) => {
  const menus = useMenus();
  const [openKeys, setOpenKeys] = useState([]);
  const property = useRef({});
  const current = useCurrentDoc();

  //  
  const isGroupRender = {},
    keyMenuMap = {},
    groupMenus = {};

  const formatMenus = (menus) => {
    const newMnes = menus.sort((n1, n2) => {
      n1.order = n1.order || 0;
      n2.order = n2.order || 0;
      return n1.order - n2.order;
    });

    newMnes.forEach((item) => {
      keyMenuMap[item.id] = item;
      let { group, menu } = item;
      if (Array.isArray(menu)) {
        //  
        // 
        menu = menu.sort((n1, n2) => {
          n1.order = n1.order || 0;
          n2.order = n2.order || 0;
          return n1.order - n2.order;
        });

        //  
        formatMenus(menu);
      } else {
        if (group) {
          groupMenus[group] = groupMenus[group] || [];
          groupMenus[group].push(item);
          isGroupRender[group] = 0;
        }
      }
    });
    return newMnes;
  };

  const renderGroup = (group) => {
    if (isGroupRender[group] === 1) return null;
    isGroupRender[group] = 1;
    if (Array.isArray(groupMenus[group])) {
      return (
        <Menu.ItemGroup key={group} title={group}>
          {groupMenus[group].map((menu, index) => {
            return (
              <Menu.Item key={menu.name}>
                <Link to={menu.route}>{menu.name}</Link>
              </Menu.Item>
            );
          })}
        </Menu.ItemGroup>
      );
    }
    return null;
  };

  const renderMenu = (menus) => {
    return menus.map((item, index) => {
      let { name, menu, id, group } = item;
      if (Array.isArray(menu)) {
        // 
        return (
          <SubMenu key={item.name} title={name}>
            {renderMenu(item.menu)}
          </SubMenu>
        );
      } else {
        return group ? (
          renderGroup(group)
        ) : (
          <Menu.Item key={item.name}>
            <Link to={item.route}>{item.name}</Link>
          </Menu.Item>
        );
      }
    });
  };
  const newMenus = formatMenus(menus);

  //   
  return (
    <Menu
      defaultSelectedKeys={[current.name]}
      openKeys={typeof window !== "undefined" && window.sessionStorage.getItem("menu") ? JSON.parse(window.sessionStorage.getItem("menu")) : openKeys}
      // defaultOpenKeys={
      //   window.sessionStorage.getItem("menu")
      //     ? JSON.parse(window.sessionStorage.getItem("menu"))
      //     : openKeys
      // }
      style={{ width: 320, height: "100%" }}
      mode="inline"
      onOpenChange={(openKeys) => {
        typeof window !== "undefined" && window.sessionStorage.setItem("menu", JSON.stringify(openKeys));
        setOpenKeys(openKeys);
      }}
    >
      {renderMenu(newMenus)}
    </Menu>
  );
};

export default SiderBar;
