import React from "react";
import { Row, Col } from "antd";

export const Header = () => {
  return (
    <Row>
      <Col
        span={4}
        style={{
          display: "flex",
          alignItems: "center",
          paddingLeft: 20,
          flexShrink: 0,
          minWidth: 320,
          // justifyContent: "center",
        }}
      >
        {/* <img src={logoZh} alt="suplink" style={{ height: 42 }} /> */}
        <span style={{ fontSize: 24, fontWeight: "bold" }}>supLink-sdk Docz</span>
      </Col>
      <Col span={20} />
    </Row>
  );
};

export default Header;
